import React from 'react';
import styled from 'styled-components'
import Emoji from "./Emoji";
import {useDispatch, useSelector} from "react-redux";
import {emojiClose, emojiOpen} from "../../redux";

const Button = styled.button`
  cursor: pointer;
  background: transparent;
  padding: 2px;
  border: none;
  :hover{
    transform: translateY(1px);
  }
`;
const Svg = styled.svg`
  width: 25px;
  height: 25px;
  color: #F9B613;
  cursor: pointer;
  @media (min-width: 768px) {
    width: 35px;
    height: 35px;
  }
`;
const EmojiBtn = () => {
    const dispatch = useDispatch();
    const open = useSelector(state => state.emoji.open);
    const handleClick = () => {
        if(open){
            dispatch(emojiClose());
            return;
        }
        dispatch(emojiOpen());
    };
    return (
        <>
            <Button onClick={handleClick}>
                <Svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="laugh-beam" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 496 512"><path fill="currentColor" d="M248 8C111 8 0 119 0 256s111 248 248 248 248-111 248-248S385 8 248 8zm141.4 389.4c-37.8 37.8-88 58.6-141.4 58.6s-103.6-20.8-141.4-58.6S48 309.4 48 256s20.8-103.6 58.6-141.4S194.6 56 248 56s103.6 20.8 141.4 58.6S448 202.6 448 256s-20.8 103.6-58.6 141.4zM328 152c-23.8 0-52.7 29.3-56 71.4-.7 8.6 10.8 11.9 14.9 4.5l9.5-17c7.7-13.7 19.2-21.6 31.5-21.6s23.8 7.9 31.5 21.6l9.5 17c4.1 7.4 15.6 4 14.9-4.5-3.1-42.1-32-71.4-55.8-71.4zm-201 75.9l9.5-17c7.7-13.7 19.2-21.6 31.5-21.6s23.8 7.9 31.5 21.6l9.5 17c4.1 7.4 15.6 4 14.9-4.5-3.3-42.1-32.2-71.4-56-71.4s-52.7 29.3-56 71.4c-.6 8.5 10.9 11.9 15.1 4.5zM362.4 288H133.6c-8.2 0-14.5 7-13.5 15 7.5 59.2 58.9 105 121.1 105h13.6c62.2 0 113.6-45.8 121.1-105 1-8-5.3-15-13.5-15z"></path></Svg>
            </Button>
            <Emoji/>
        </>
    );
};

export default EmojiBtn;